import { Sampler } from "tone";
import { ChordBacking, DetailChord, VoicingProps } from "../redux/store/scoreData";
import { getSymbolFromKey, IntervalRelationProps, KEY12_SHARP_LIST, SymbolAttribute } from "./theoryUtils";

export const getDefaultSampler = () => {
    return new Sampler({
        "C2": "https://dl.dropbox.com/s/9v1e4vb2y49kvxe/KEPSREC036.mp3?dl=0",
        "C3": "https://dl.dropbox.com/s/qjkoqqdkqwtnctj/KEPSREC048.mp3?dl=0",
        "C4": "https://dl.dropbox.com/s/9fgqyu7hsj8fncc/KEPSREC060.mp3?dl=0",
        "C5": "https://dl.dropbox.com/s/0v042zfk67q4a8d/KEPSREC072.mp3?dl=0",
        "C6": "https://dl.dropbox.com/s/3u2c7qsmhnse575/KEPSREC084.mp3?dl=0",
    }).toDestination();
}

export const playChord = (sampler: Sampler, detail: DetailChord, bpm: number) => {

    sampler.releaseAll();
    if (detail.root == null) return;

    const rootIndex = detail.root.index;
    const symbol = getSymbolFromKey(detail.symbolKey);

    const channelList = detail.backing.voicingList.map((item) => {
        const interval = IntervalRelationProps[symbol.structs[item.struct]];
        return 12 * item.octave + rootIndex + interval;
    });

    channelList.forEach((index, i) => {
        const soundName = KEY12_SHARP_LIST[index % 12];
        const octave = Math.floor(index / 12);
        const soundFullName = soundName + octave;

        sampler.triggerAttack(soundFullName, '+0', 0.5);
        // setTimeout(() => {
        //     sampler.triggerAttack(soundFullName, '+0', 0.5);
        // }, 20 * i);
    });

    setTimeout(() => {
        // sampler.triggerRelease('C3');
        sampler.releaseAll();
    }, 2000);
}


export const getDefaultBacking = (attr: SymbolAttribute) => {
    let voicingList: VoicingProps[] = [];
    switch (attr) {
        case 'triad': voicingList = [
            { struct: 0, octave: 3 },
            { struct: 1, octave: 3 },
            { struct: 2, octave: 3 }
        ]; break;
        case 'seventh': voicingList = [
            { struct: 0, octave: 3 },
            { struct: 1, octave: 3 },
            { struct: 2, octave: 3 },
            { struct: 3, octave: 3 }
        ]; break;
        case 'add9th': voicingList = [
            { struct: 0, octave: 3 },
            { struct: 1, octave: 3 },
            { struct: 2, octave: 3 },
            { struct: 3, octave: 3 }
        ]; break;
    }
    const backing: ChordBacking = {
        voicingList,
        pattern: null
    };
    return backing;
}