import { KeyboardEvent } from "react";
import { Dispatcher } from "../../../../../redux/dispatcher/dispatcher";
import { MelodyPitchMode } from "../../../../../redux/store/melodyState";
import { Store } from "../../../../../redux/store/store";
import { getNormalizeLenFromNotes } from "../../../../../utils/melodyUtil";
import { TUNE_PITCH_HEIGHT } from "../../../../../utils/systemConst";


class MelodyInput {

    static keyDownEvent = (e: KeyboardEvent, store: Store, dispatcher: Dispatcher) => {

        const config = store.userEnv.keyConfig.note;
        const keySwitch = store.melodyState.keySwitch;
        // alert(e.key);
        // alert(config.nextElement);

        const state = store.melodyState;
        const mouse = state.mouseState;

        // 
        if (mouse.notesState.index !== -1) {
            mouse.notesState.index = -1;
            mouse.notesState.isResize = false;
            mouse.notesState.side = 'normal';
        }

        if (keySwitch.scaleMode || keySwitch.octave || keySwitch.resize || keySwitch.move) {
            if (keySwitch.scaleMode || keySwitch.octave) {
                const mode: MelodyPitchMode = keySwitch.scaleMode ? 'scale' : 'octave';
                switch (e.key) {
                    case 'ArrowUp': {
                        dispatcher.tune.incrementCurNoteKey(true, mode);
                    }
                        break;
                    case 'ArrowDown': {
                        dispatcher.tune.incrementCurNoteKey(false, mode);
                    }
                        break;
                }
            } else if (keySwitch.resize) {
                switch (e.key) {
                    case 'ArrowLeft': dispatcher.tune.resizeNotes(false); break;
                    case 'ArrowRight': dispatcher.tune.resizeNotes(true); break;
                }
            } else if (keySwitch.move) {
                switch (e.key) {
                    case 'ArrowLeft': dispatcher.tune.moveNotes(false); break;
                    case 'ArrowRight': dispatcher.tune.moveNotes(true); break;
                }
            }
        } else {
            switch (e.key) {
                case 'ArrowUp': dispatcher.tune.incrementCurNoteKey(true, 'normal'); break;
                case 'ArrowDown': dispatcher.tune.incrementCurNoteKey(false, 'normal'); break;
                case 'ArrowLeft': dispatcher.tune.moveFocus(false); break;
                case 'ArrowRight': dispatcher.tune.moveFocus(true); break;
                case 'a': dispatcher.tune.addNotes(); break;
                case 'q': {
                    alert(JSON.stringify(store.scoreData.notesList[state.focusIndex].len));
                }
                    break;
                case 'w': dispatcher.tune.adjustScrollX(); break;
                case 'e': dispatcher.tune.adjustScrollY(); break;
            }
        }

        switch (e.key) {
            case '1': {
                state.quantize = 4;
                dispatcher.tune.setState(state);
            }
                break;
            case '2': {
                state.quantize = 2;
                dispatcher.tune.setState(state);
            }
                break;
            case '3': {
                state.quantize = 1;
                dispatcher.tune.setState(state);
            }
                break;
            case '/': {
                if ([3, 6].includes(state.quantize)) {
                    state.quantize /= 3;
                } else {
                    state.quantize *= 3;
                }
                dispatcher.tune.setState(state);
            }
                break;
            case 'Delete': {
                const notesList = store.scoreData.notesList;
                notesList.splice(state.focusIndex, 1);
                dispatcher.tune.setNotesList(notesList);
                state.focusIndex--;
                dispatcher.tune.setState(state);
            }
                break;
            case ' ': {
                dispatcher.tune.previewStart();
            }
                break;
        }
    }

    static keySwitchEvent = (e: KeyboardEvent, store: Store, dispatcher: Dispatcher, isPress: boolean) => {

        const config = store.userEnv.keyConfig.note;
        const keySwitch = store.melodyState.keySwitch;
        // alert(e.key);
        // alnfig.nextElement);
        switch (e.key) {
            case 'z': {
                keySwitch.octave = isPress;
                dispatcher.tune.setKeySwitch(keySwitch);
            }
                break;
            case 'c': {
                keySwitch.scaleMode = isPress;
                dispatcher.tune.setKeySwitch(keySwitch);
            }
                break;
            case 'f': {
                keySwitch.resize = isPress;
                dispatcher.tune.setKeySwitch(keySwitch);
            }
                break;
            case 'd': {
                keySwitch.move = isPress;
                dispatcher.tune.setKeySwitch(keySwitch);
            }
                break;
        }

        if (isPress) {
            MelodyInput.keyDownEvent(e, store, dispatcher);
        }
    }
}

export default MelodyInput;