import { useContext } from "react";
import styled from "styled-components";
import { BackingState } from "../../../../redux/store/backingState";
import { PATTERN_BODY_HEIGHT, PATTERN_FOOTER_HEIGHT, PATTERN_HEADER_HEIGHT } from "../../../../utils/systemConst";
import { GlobalContext } from "../../../entry/systemEntry";
import PatternLength from "./patternLength";
import PatternTable from "./patternTable";

const PatternBuilder = () => {

    const { store, dispatcher } = useContext(GlobalContext);

    const state = store.backingState as BackingState;
    const chordInfo = state.chordInfo;

    const isUsePattern = state.pattern != null;

    const update = () => {
        dispatcher.backing.setState(state);
    }

    return (<>
        <_Header>
            <_LengthDiv>
                <PatternLength />
            </_LengthDiv>
            <_MeasureDiv>
            </_MeasureDiv>
        </_Header>
        <_Body>
            <PatternTable />
        </_Body>
        <_Footer>
        </_Footer>
    </>);
}

export default PatternBuilder;

const _Header = styled.div`
    display: inline-block;
    width: 100%;
    height: ${PATTERN_HEADER_HEIGHT}px;
    background-color: #9292bdd6;
`;
const _LengthDiv = styled.div`
    display: inline-block;
    width: 100%;
    height: ${PATTERN_HEADER_HEIGHT / 2}px;
    background-color: #9292bdd6;
`;
const _MeasureDiv = styled.div`
    display: inline-block;
    width: 100%;
    height: ${PATTERN_HEADER_HEIGHT / 2}px;
    background-color: #b492bdd5;
`;
const _Body = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: ${PATTERN_BODY_HEIGHT}px;
    background-color: #97d3cec8;
`;
const _Footer = styled.div`
    display: inline-block;
    width: 100%;
    height: ${PATTERN_FOOTER_HEIGHT}px;
    background-color: #898997cc;
`;