import { useContext } from "react";
import styled, { css } from "styled-components";
import { ELEMENT_POS_INFO_WIDTH } from "../../../../../../utils/systemConst";
import { GlobalContext } from "../../../../../entry/systemEntry";

const CalcChange = (props: {
    index: number;
}) => {

    const { store, dispatcher } = useContext(GlobalContext);

    const state = store.scoreState;

    return (
        <_Wrap>
            <_Type>Key</_Type>
            <_Detail>CAm</_Detail>
        </_Wrap>
    );
}

export default CalcChange;

const _Wrap = styled.div`
    display: inline-block;
    position: absolute;
    z-index: 4;
    width: 100%;
    height: 50px;
    /* background-color: #ff000011; */
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    text-align: center;
    box-sizing: border-box;
`;

const _Label = styled.div`
    display: block;
    position: relative;
    /* min-width: 80px; */
    height: 16px;
    font-size: 14px;
    font-weight: 600;
    text-align: center;
    line-height: 16px;
    /* background-color: #00000042; */
`;

const _Type = styled(_Label)`
    color: #0b00a8cf;
`;

const _Detail = styled(_Label)`
    color: #050505cf;
`;