import { useContext } from "react";
import styled from "styled-components";
import { ELEMENT_NOTE_PADDING_LEN, ELEMENT_NOTE_PADDING_SID } from "../../../../utils/systemConst";
import { GlobalContext } from "../../../entry/systemEntry";
import ScoreElement from "./elements/scoreElement";
import SymbolEditor from "./input/symbolEditor";


const HarmNote = () => {
    const { store, dispatcher } = useContext(GlobalContext);

    const num = store.scoreData.chordList.length;

    // alert(`num=${num}, cache=${store.scoreState.elementCacheList.length}`);
    const list: JSX.Element[] = [];
    if (store.scoreState.elementCacheList.length === num) {
        for (let i = 0; i < num; i++) {
            list.push(
                <ScoreElement key={i} index={i} />
            );
        };
    }
    return (
        <_Wrap>
            {/* <_Frame> */}
            <_Note>
                {list}
            </_Note>
            <_Note>
            </_Note>
            {/* </_Frame> */}
        </_Wrap>
    );
}

export default HarmNote;


const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    margin: 4px 0 0 4px;
    width: calc(100% - 8px);
    height: calc(100% - 8px);
    /* background-color: #e7dddd; */
    background-color: #e9e9e9;
    border: 1px solid #b3b3b3;
    border-radius: 2px;
    box-sizing: border-box;
    text-align: left;
    overflow: auto;
    
    &::-webkit-scrollbar {
        width: 10px;
        height: 10px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 5px;
        background: #1959A8;
    }
    &::-webkit-scrollbar-track {
        border-radius: 5px;
        box-shadow: 0 0 4px #aaa inset;
    }
`;

const _Frame = styled.div`
    transform-origin: top left;
    /* width: 100%;
    height: 100%; */
    transform: scale(1);
`;

const _Note = styled.div`
    display: inline-block;
    /* position: relative; */
    padding: ${ELEMENT_NOTE_PADDING_LEN}px ${ELEMENT_NOTE_PADDING_SID}px;
    width: ${891}px;
    height: ${1260}px;
    background-color: #f1f1f1;
    /* overflow: hidden; */
    margin: 10px 0 0 10px;
    box-shadow: 0 0 18px 0 #000b3a48;
`;