import { useContext } from "react";
import styled from "styled-components";
import { BackingPattern, BackingState } from "../../../../redux/store/backingState";
import { getSymbolFromKey, IntervalRelationProps, KEY12_SHARP_LIST } from "../../../../utils/theoryUtils";
import { PATTERN_BODY_HEIGHT, PATTERN_FOOTER_HEIGHT, PATTERN_HEADER_HEIGHT } from "../../../../utils/systemConst";
import { GlobalContext } from "../../../entry/systemEntry";
import ChannelItem from "./channelItem";

const PatternChannel = () => {

    const { store, dispatcher } = useContext(GlobalContext);

    const state = store.backingState as BackingState;
    const chordInfo = state.chordInfo;
    const symbol = getSymbolFromKey(chordInfo.symbolKey);
    const pattern = state.pattern;

    const channelJsxList: JSX.Element[] = [];
    if (pattern != null) {
        const channels = pattern.channels;
        channels.forEach((channel, i) => {
            let name = '-';
            if (channel.octave !== -1) {
                const soundIndex = chordInfo.chordRoot + IntervalRelationProps[symbol.structs[channel.structIndex]];
                const octave = channel.octave + Math.floor(soundIndex / 12);
                name = KEY12_SHARP_LIST[soundIndex % 12] + octave;
            }
            channelJsxList.push(
                <ChannelItem key={i}
                    soundFullName={name}
                />
            );
        });
    }

    return (<>
        <_Header>
        </_Header>
        <_Body>
            {channelJsxList}
        </_Body>
        <_Footer>
        </_Footer>
    </>);
}

export default PatternChannel;

const _Header = styled.div`
    /* display: inline-block; */
    width: 100%;
    height: ${PATTERN_HEADER_HEIGHT}px;
    background-color: #9292bdd6;
`;
const _Body = styled.div`
/* display: inline-block; */
    width: 100%;
    height: ${PATTERN_BODY_HEIGHT}px;
    background-color: #97d3cec8;
`;
const _Footer = styled.div`
/* display: inline-block; */
    width: 100%;
    height: ${PATTERN_FOOTER_HEIGHT}px;
    background-color: #898997cc;
`;