import styled from "styled-components";
import { DetailChange } from "../../../../../../redux/store/scoreData";

const ElementDetailChange = (props: {
    detail: DetailChange;
}) => {

    return (
        <_Wrap>
            <_Type>
                Modulation
            </_Type>
            <_Detail>
                Domm+1
            </_Detail>
        </_Wrap>
    );
}

export default ElementDetailChange;

const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: red; */
`;

const _Label = styled.div`
    display: block;
    position: relative;
    min-width: 100px;
    height: 20px;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    line-height: 20px;
    /* background-color: #1000eb42; */
    margin-top: 3px;
`;

const _Type = styled(_Label)`
    color: #0014cc;
`;

const _Detail = styled(_Label)`
    color: #5e5e5e;
`;