export const getMinimumLenUnit = (notes: MelodyLength): number => {

    if (notes.len8div3 > 0) return 6;
    else if (notes.len16 > 0) return 4;
    else if (notes.len4div3 > 0) return 3;
    else if (notes.len8 > 0) return 2;
    else if (notes.len4 > 0) return 1;
    else return 0;
}

export const playSoundfont = (i: number, store: Store) => {
    const keyIndex = ((59 - i) + 9) % 12;
    const octave = Math.floor(((59 - i) + 9) / 12) + 1;
    const soundName = KEY12_SHARP_LIST[keyIndex % 12];
    const instrument = store.instruments.soundfontPlayer;
    if (instrument != null) {
        instrument.stop();
        instrument.play(soundName + octave, 0, { duration: 0.5 });
    }
}


export const previewStart = (state: MelodyState, data: ScoreData, instrument: Soundfont.Player) => {

    const init = data.chordList[0].detail as DetailInit;
    const bpm = init.bpm;

    for (let i = 0; i < data.notesList.length; i++) {
        const notes = data.notesList[i];
        const pos = getNormalizeLenFromNotes(notes.pos);
        const len = getNormalizeLenFromNotes(notes.len);
        const pitch = notes.pitchIndex;
        const time = 60 / bpm * pos;

        setTimeout(() => {
            if (instrument != null) {
                const keyIndex = getKeyIndexFromPitcher(pitch);
                const soundName = KEY12_SHARP_LIST[keyIndex % 12];
                const octave = Math.floor(((59 - i) + 9) / 12) + 1;
                instrument.stop();
                instrument.play(soundName + octave, 0, { duration: 0.5 });
            }
        }, time * 1000);
    }
}
