import { ColorProps } from "../../base/design/colorProps";

export type UserEnv = {
    keyConfig: KeyConfig;
    colorProps: ColorProps;
}

export type KeyConfig = {
    note : {
        prevElement: string;
        nextElement: string;
        prevSection: string;
        nextSection: string;
        addChord: string;
        addSection: string;
        addChange: string;
        deleteCur: string;
        editMode: string;
        incRoot: string;
        decRoot: string;
        incBeat: string;
        decBeat: string;
        symbolMode: string;
    }
}

export const getDefaultKeyConfig = ()=> {
    return {
        note: {
            prevElement: 'ArrowLeft',
            nextElement: 'ArrowRight',
            prevSection: 'ArrowUp',
            nextSection: 'ArrowDown',
            addChord: 'a',
            addSection: 's',
            addChange: 'f',
            deleteCur: 'Delete',
            editMode: 'Shift',
            incRoot: 'ArrowUp',
            decRoot: 'ArrowDown',
            incBeat: 'ArrowRight',
            decBeat: 'ArrowLeft',
            symbolMode: 'c',
        }
    } as KeyConfig;
}